import { createCustomElement } from '@servicenow/ui-core';

import '@servicenow/now-icon';
import '../devx-tooltip-icon';
import '../devx-multiselect';

import styles from './_devx-select.scss';

export const INPUT_CHANGED = 'DEVX_SELECT#INPUT_CHANGED';

const SingleSelect = ({
	id,
	disabled,
	dispatch,
	inputValue = '',
	name,
	options,
	placeholder,
	size
}) => (
	<div className="single-select-wrapper">
		<select
			class={{ [size ? `-${size}` : '-block']: true }}
			disabled={disabled}
			id={id}
			name={name}
			on-change={({ target: { value } }) => dispatch(INPUT_CHANGED, { value })}
			required
			value={inputValue}>
			{placeholder && (
				<option value="" selected={!inputValue} disabled>
					{placeholder}
				</option>
			)}
			{options &&
				options.map(({ value, text }) => (
					<option value={value} selected={value === inputValue}>
						{text}
					</option>
				))}
		</select>
		<now-icon className="caret-icon" icon="caret-down-fill" />
	</div>
);

const isValidItem = (item, textKey, valueKey) => {
	const isValid =
		typeof item === 'object' &&
		!Array.isArray(item) &&
		item.hasOwnProperty(textKey) &&
		item.hasOwnProperty(valueKey);

	if (!isValid) {
		console.error(
			`Items provided to multiselect must be an Array of Objects with ${textKey} and ${valueKey} keys. ${JSON.stringify(
				item
			)} was the provided item.`
		);
	}

	return isValid;
};

const MultiSelect = ({
	disabled,
	options,
	placeholder,
	size,
	label,
	inputValue = [],
	itemIdKeyName,
	itemDisplayKeyName,
	errorMsg
}) => {
	const filteredSelectedItems = inputValue.filter((selectedItem) => {
		return isValidItem(selectedItem, itemDisplayKeyName, itemIdKeyName);
	});

	const sortedOptions = options
		.filter((item) => {
			const isValid = isValidItem(item, itemDisplayKeyName, itemIdKeyName);
			if (!isValid) {
				return false;
			}

			return !filteredSelectedItems.some((selectedItem) => {
				return selectedItem[itemIdKeyName] === item[itemIdKeyName];
			});
		})
		.sort((a, b) => {
			const aNormalized = a[itemDisplayKeyName] || '';
			return aNormalized.localeCompare(b[itemDisplayKeyName] || '');
		});
	return (
		<devx-multiselect
			label={label}
			placeholder={placeholder}
			items={sortedOptions}
			selectedItems={filteredSelectedItems}
			size={size}
			disabled={disabled}
			itemDisplayKeyName={itemDisplayKeyName}
			errorMsg={errorMsg}
		/>
	);
};

const GacSelect = (
	{
		componentId,
		properties: { multiple, label, tooltip, errorMsg, helpText, name, ...selectOptions }
	},
	{ dispatch }
) => {
	// eslint-disable-next-line no-unused-vars
	const Select = multiple ? MultiSelect : SingleSelect;

	return (
		<div class={{ '-show-error': errorMsg }}>
			{label && (
				<div className="now-align">
					<label className="devx-input-label now-m-inline-end--xs" htmlFor={componentId}>
						{label}
					</label>
					{tooltip ? <devx-tooltip-icon name={name} text={tooltip} /> : null}
				</div>
			)}

			<Select
				id={componentId}
				dispatch={dispatch}
				label={label}
				errorMsg={errorMsg}
				{...selectOptions}
			/>
			<span className="devx-input-help-text">{errorMsg || helpText}</span>
		</div>
	);
};

/**
 * Select
 *
 * @seismicElement devx-select
 * @summary A native dropdown select component.
 */
createCustomElement('devx-select', {
	properties: {
		/**
		 * The keyname of the item value
		 * @type {string}
		 */
		itemIdKeyName: { default: 'value' },

		/**
		 * The keyname for the item display label
		 * @type {string}
		 */
		itemDisplayKeyName: { default: 'text' },

		/**
		 * The value for this input.
		 * @type {any}
		 */
		inputValue: {},
		/**
		 * A name for the input control, passed along with the value when the form data is submitted.
		 * And used when passing in error state to the form
		 * @type {string}
		 */
		name: {
			default: ''
		},
		/**
		 * Set to true to disable this input.
		 * @type {boolean}
		 */
		disabled: {
			default: false,
			reflect: true
		},
		/**
		 * A placeholder string to be displayed inside of the input field.
		 * @type {string}
		 */
		placeholder: {
			default: ''
		},
		/**
		 * An array of option objects.
		 * @type {{text: string, value: any}[]}
		 */
		options: {
			default: null
		},
		/**
		 * Label to be displayed above the input field.
		 * @type {string}
		 */
		label: {
			default: ''
		},
		/**
		 * Determines whether multiple options can be selected or just one.
		 * @type {boolean}
		 */
		multiple: {
			default: false
		},
		/**
		 * Size of the input field.
		 * If none is provided, input is sized automatically.
		 * @type {('sm'|'md'|'lg'|'fullwidth')}
		 */
		size: {
			default: null
		},
		/**
		 * Tooltip text to display when icon is hovered
		 * @type {string}
		 */
		tooltip: {
			default: ''
		},
		/**
		 * Help or hint text to display below the input field.
		 * @type {string}
		 */
		helpText: {},
		/**
		 * Error message to display, sets error styles
		 * @type {string}
		 */
		errorMsg: {}
	},
	view: GacSelect,
	styles,
	actionHandlers: {
		// Normalizes actions from the multi-select. The only action a user of
		// `<devx-select>` needs to register is `INPUT_CHANGED`.
		'DEVX_MULTISELECT_PANEL#SELECTED_ITEMS_SET': ({ action, dispatch }) => {
			dispatch(INPUT_CHANGED, { value: action.payload.value });
		}
	}
});
