import { createEnhancedElement } from '@servicenow/library-enhanced-element';
import { actionTypes } from '@servicenow/ui-core';
import { t } from 'sn-translate';

import keyBindingBehavior from '@servicenow/behavior-key-binding';
import focusBehavior from '@servicenow/behavior-focus';
import '@servicenow/now-button';
import '@servicenow/now-icon';
import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';
import { getFocusedEl, getPanelEl } from './utils/elements';
import { focusFirstItem } from './utils/focus';
import styles from './_devx-typeahead.scss';
import {
	ITEM_CLICKED as PANEL_ITEM_CLICKED,
	OPENED_SET as PANEL_OPENED_SET,
	OUTSIDE_CLICKED as PANEL_OUTSIDE_CLICKED,
	OUTSIDE_SCROLLED as PANEL_OUTSIDE_SCROLLED,
	DevxTypeaheadPanelItem // eslint-disable-line
} from './devx-typeahead-panel';

import {
	INPUT_BLUR,
	INPUT_CHANGED as INPUT_INPUT_CHANGED,
	INPUT_FOCUS,
	INPUT_CLICKED,
	INPUT_ICON_BUTTON_CLICKED
} from '../devx-input/devx-input';

const { COMPONENT_DOM_READY, COMPONENT_PROPERTY_CHANGED } = actionTypes;

export const INPUT_CHANGED = 'DEVX_TYPEAHEAD#INPUT_CHANGED';
export const ITEM_CLICKED = 'DEVX_TYPEAHEAD#ITEM_CLICKED';
export const OPENED_SET = 'DEVX_TYPEAHEAD#OPENED_SET';
export const UPDATE_CURATED_ITEMS = 'DEVX_TYPEAHEAD#UPDATE_CURATED_ITEMS';
export const UPDATE_SELECTED_OPTION = 'DEVX_TYPEAHEAD#UPDATE_SELECTED_OPTION';
export const FOCUS_ON_INPUT = 'DEVX_TYPEAHEAD#FOCUS_ON_INPUT';
export const INVALID_ITEM_CLICKED = 'DEVX_TYPEAHEAD#INVALID_ITEM_CLICKED';
export const FOCUS_ON_ITEM = 'DEVX_TYPEAHEAD#FOCUS_ON_ITEM';

const getSelectedOption = (option, items) => {
	return items.find(({ id = '' }) => {
		return option === id;
	});
};

/**
 * Typeahead
 *
 * ```jsx
 * <devx-typeahead
 * 	manage-items
 * 	items={[
 * 		{
 * 			id: 0,
 * 			label: 'lazy learning'
 * 		},
 * 		{
 * 			id: 1,
 * 			label: 'horrible harbor'
 * 		}
 * 	]}
 * 	placeholder="Select an item"
 * 	label="Label here"
 * 	loading={false}
 * 	name="name"
 * 	selected-item={selectedItem}
 * 	errorMsg="Select a Pokmon"
 *  />
 * ```
 *
 * @seismicElement devx-typeahead
 * @summary An input component that give a user suggested search results
 */
createEnhancedElement('devx-typeahead', {
	view: (state, { dispatch, updateState }) => {
		const {
			componentId,
			curatedItems,
			opened,
			focusedOption,
			currentInputValue,
			fitTarget
		} = state;
		const {
			compact,
			disabled,
			errorMsg,
			helpText,
			icon,
			label,
			loading,
			name,
			noResultsMsg,
			placeholder,
			required,
			secondaryLabel,
			tooltip,
			transparent,
			selectedItem,
			container
		} = state.properties;
		const noResults = !isEmpty(currentInputValue) && curatedItems.length === 0;
		let anyErrorMsg = errorMsg;

		// The design calls for both the input and panel to be in an error state
		// when there are no results.
		if (noResults && !anyErrorMsg) {
			anyErrorMsg = noResultsMsg;
		}

		const selectedOption =
			curatedItems && (selectedItem || selectedItem === 0)
				? curatedItems.find((i) => i.id === selectedItem)
				: null;

		const newInputValue =
			(selectedOption && selectedOption.label) || currentInputValue || '';

		return (
			<div className="devx-typeahead">
				<devx-input
					override-role="combobox"
					compact={compact}
					disabled={disabled}
					errorMsg={anyErrorMsg}
					fitTarget={fitTarget}
					helpText={helpText}
					hook-destroy={() => updateState({ fitTarget: undefined })}
					hook-insert={({ elm }) => updateState({ fitTarget: elm })}
					icon={icon}
					id={`input-${componentId}`}
					input-value={newInputValue}
					label={label}
					name={name}
					placeholder={placeholder}
					required={required}
					secondary-label={secondaryLabel}
					tooltip={tooltip}
					transparent={transparent}
					configAria={{
						'aria-autocomplete': 'both',
						'aria-haspopup': 'listbox',
						'aria-owns': `panel-${componentId}`,
						'aria-activedescendant': focusedOption
							? `${focusedOption.id}-${componentId}`
							: null,
						'aria-expanded': `${opened || false}`
					}}
				/>

				{disabled ? null : (
					<devx-typeahead-panel
						compact={compact}
						disabled={disabled}
						id={`panel-${componentId}`}
						hasError={!isEmpty(anyErrorMsg)}
						manage-opened
						opened={opened}
						target-ref={fitTarget}
						container={container}
						items={
							loading
								? [<DevxTypeaheadPanelItem label={t('loading')} />]
								: noResults
								? [
										<DevxTypeaheadPanelItem
											selectable={false}
											label={noResultsMsg}
											leftGutter={
												<now-icon
													className="devx-typeahead-panel-item-no-results-icon"
													icon="triangle-exclamation-outline"
													aria-label={t(label)}
												/>
											}
										/>
								  ]
								: isEmpty(curatedItems)
								? [
										<DevxTypeaheadPanelItem
											selectable={false}
											label={t('Type to search...')}
										/>
								  ]
								: curatedItems.map((option) => (
										<DevxTypeaheadPanelItem
											disabled={option.disabled}
											key={`${option.id}-${componentId}`}
											label={option.label}
											onClick={() => {
												dispatch(ITEM_CLICKED, { option });
												dispatch(OPENED_SET, { value: false });
											}}
											onFocus={() => dispatch(FOCUS_ON_ITEM, { option })}
											selected={option.id === get(selectedOption, 'id', '')}
											setsize={curatedItems.length}
											value={option.id}
										/>
								  ))
						}
					/>
				)}
			</div>
		);
	},
	styles,
	properties: {
		/**
		 * Flag for omitting space below input. WILL prevent any errorMsg or helpText from displaying
		 * @type {boolean}
		 */
		compact: { default: false, reflect: true },
		/**
		 * Set to true to disable this input.
		 * @type {boolean}
		 */
		disabled: { default: false },
		/**
		 * Error message to display, sets error styles
		 * Also sets the aria-invalid property
		 * @type {string}
		 */
		errorMsg: { default: null },
		/**
		 * Error message to display, sets error styles
		 * Also sets the aria-invalid property
		 * @type {string}
		 */
		helpText: {},
		/**
		 * A ServiceNow icon value to be used as a button in the input
		 * @type {string}
		 * @example 'caret-down-outline'
		 */
		icon: { default: 'caret-down-outline' },
		/**
		 * Items to be displayed as search results
		 * @type {{id: string, label: string, disabled: boolean}[]}
		 */
		items: {
			default: [],
			cast: 'array'
		},
		/**
		 * Label to be displayed above the input field.
		 * @type {string}
		 */
		label: { default: '' },
		/**
		 * Determines if the typeahead panel is in the loading state.
		 * This should be set when requesting new results but they haven't been returned yet.
		 * @type {boolean}
		 */
		loading: { default: false },
		/**
		 * When manageItems is set to true, the filtering of the items happens within typeahead.
		 * If manageItems is false, it is on the user to manage the filtering of the items.
		 * @type {boolean}
		 */
		manageItems: { default: false },
		/**
		 * A name for the input control, passed along with the value when the form data is submitted.
		 * And used when passing in error state to the form
		 * @type {string}
		 */
		name: {},
		/**
		 * noResultsMsg is message the message that is displayed to the user when no results are found.
		 * This allow the user to override the default message.
		 * @type {string}
		 */
		noResultsMsg: { default: t('No results...') },
		/**
		 * A placeholder string to be displayed inside of the input field.
		 * @type {string}
		 */
		placeholder: { default: '' },
		/**
		 * Set to true to mark the input as required.
		 * @type {boolean}
		 */
		required: { default: false },
		/**
		 * Secondary label to be displayed above the input field, but below the
		 * label.
		 * @type {string}
		 */
		secondaryLabel: { default: '' },
		/**
		 * An id string of the selected item
		 * @type {string}
		 */
		selectedItem: {
			default: null
		},
		/**
		 *	Tooltip text to show additional information
		 *  @type {string}
		 */
		tooltip: { default: '' },
		/**
		 * Flag for setting the background color of the input to transparent.
		 * @type {boolean}
		 */
		transparent: { default: false, reflect: true },
		/**
		 * The container that the typeahead panel should be constrained within.
		 * @type {HTMLElement}
		 */
		container: { default: null }
	},
	initialState: {
		curatedItems: null,
		inputHasFocus: false,
		selectedOption: null,
		focusedOption: null,
		opened: false,
		currentInputValue: null,
		fitTarget: {}
	},
	actionHandlers: {
		[COMPONENT_DOM_READY]: ({ properties, updateState, state }) => {
			const { items, selectedItem } = properties;
			const { selectedOption } = state;

			if (selectedItem && !selectedOption) {
				const option = getSelectedOption(selectedItem, items);
				updateState({
					selectedOption: option
				});
			}

			updateState({
				curatedItems: items
			});
		},
		[COMPONENT_PROPERTY_CHANGED]: ({ action, dispatch }) => {
			const { name, value } = action.payload;

			switch (name) {
				case 'selectedItem':
					dispatch(UPDATE_SELECTED_OPTION, { value: { id: value } });
					break;

				case 'items':
					dispatch(UPDATE_CURATED_ITEMS, { value });
					break;
			}
		},
		[INPUT_INPUT_CHANGED]: ({ state, action, dispatch, updateState }) => {
			action.stopPropagation();
			const { manageItems, items } = state.properties;
			const { value: currentInputValue } = action.payload;

			let curatedItems = items || [];

			if (items && !manageItems && !isEmpty(currentInputValue)) {
				const pattern = new RegExp(
					// Search for problematic characters and filter out
					currentInputValue.replace(/[#-.]|[[-^]|[?|{}]/g, '\\$&'),
					'ig'
				);
				curatedItems = items.filter(({ label }) => label.match(pattern));
			}

			dispatch(INPUT_CHANGED, { value: currentInputValue });
			dispatch(OPENED_SET, { value: true });

			updateState({
				curatedItems,
				currentInputValue
			});
		},
		[INPUT_CLICKED]: ({ state, dispatch, action }) => {
			action.stopPropagation();
			const { opened } = state;
			dispatch(OPENED_SET, { value: !opened });
		},
		[INPUT_FOCUS]: ({ action, updateState }) => {
			action.stopPropagation();

			updateState({
				inputHasFocus: true
			});
		},
		[FOCUS_ON_INPUT]: ({ state }) => {
			state.fitTarget.shadowRoot.querySelector('input').focus();
		},
		[FOCUS_ON_ITEM]: ({ action, updateState }) => {
			const { option } = action.payload;
			updateState({
				focusedOption: option
			});
		},
		[INPUT_BLUR]: ({ action, updateState, state, dispatch }) => {
			action.stopPropagation();
			const { value } = action.payload;

			// If there is no selected option or there is text in the input that does not
			// match the selected option, dispatch an action with the current input value
			// so that error messages can be set/passed down from parent components.
			if (!state.selectedOption || value !== state.selectedOption.label) {
				dispatch(INVALID_ITEM_CLICKED, { currentInputValue: value });
			}

			updateState({
				currentInputValue: value,
				inputHasFocus: false
			});
		},
		[OPENED_SET]: ({ action, updateState }) => {
			const { value: opened } = action.payload;

			updateState({
				opened
			});
		},
		[PANEL_ITEM_CLICKED]: ({ action, dispatch }) => {
			action.stopPropagation();
			const { option } = action.payload;

			dispatch(ITEM_CLICKED, { option });
		},
		[ITEM_CLICKED]: ({ action, dispatch, state, updateState }) => {
			const { option } = action.payload;

			updateState({
				inputHasFocus: false
			});

			state.fitTarget.shadowRoot.querySelector('input').focus();

			dispatch(UPDATE_SELECTED_OPTION, { value: option });
			dispatch(OPENED_SET, { value: false });
		},
		[PANEL_OPENED_SET]: ({ action, dispatch }) => {
			action.stopPropagation();

			const { value: opened } = action.payload;

			dispatch(OPENED_SET, { value: opened });
		},
		[INPUT_ICON_BUTTON_CLICKED]: ({ dispatch, state, action }) => {
			action.stopPropagation();
			const { opened } = state;

			if (opened) {
				dispatch(OPENED_SET, { value: false });
			} else {
				dispatch(OPENED_SET, { value: true });
				state.fitTarget.shadowRoot.querySelector('input').focus();
			}
		},
		[PANEL_OUTSIDE_CLICKED]: ({ action, dispatch, state }) => {
			action.stopPropagation();

			const { currentInputValue, inputHasFocus } = state;
			const { items } = state.properties;

			if (!inputHasFocus) {
				// Update the selected option, if changed.
				if (items && !items.some(({ label }) => label === currentInputValue)) {
					dispatch(UPDATE_SELECTED_OPTION, {
						value: { label: currentInputValue }
					});
				}
				dispatch(OPENED_SET, { value: false });
			}
		},
		[PANEL_OUTSIDE_SCROLLED]: ({ action, dispatch, updateState }) => {
			action.stopPropagation();

			dispatch(OPENED_SET, { value: false });

			updateState({
				inputHasFocus: false
			});
		},
		[UPDATE_CURATED_ITEMS]: ({ action, updateState }) => {
			const { value } = action.payload;

			updateState({
				curatedItems: value || []
			});
		},
		[UPDATE_SELECTED_OPTION]: ({ action, state, updateState }) => {
			const { curatedItems } = state;
			const { id, label } = get(action, 'payload.value', {
				id: null,
				label: null
			});

			let selectedOption = {};

			if ((id || id === 0) && label) {
				selectedOption = { id, label };
			} else if ((id || id === 0) && !label) {
				selectedOption =
					(!isEmpty(curatedItems) && curatedItems.find((i) => i.id === id)) || {};
			} else if (label) {
				selectedOption = { label };
			}

			updateState({
				selectedOption,
				currentInputValue: selectedOption.label || ''
			});
		},
		/**
		 * A key binding action handler that provides a way to access the component
		 * state when handling key events.
		 */
		['FOCUS_KEY_BINDING']: ({ action, dispatch, state }) => {
			action.stopPropagation();

			const { currentInputValue, inputHasFocus, opened } = state;
			const { event, host, key } = action.payload;

			if (key === 'ArrowDown') {
				// When down arrow pressed, open panel and select first item.
				event.preventDefault();

				dispatch(OPENED_SET, { value: true });

				const panel = getPanelEl(host);
				panel.focus();
				focusFirstItem(panel);
			} else if (!opened) {
				// When panel closed, place focus on input. Panel will open.
				event.preventDefault();
				state.fitTarget.shadowRoot.querySelector('input').focus();
			} else if ((inputHasFocus && isEmpty(currentInputValue)) || !inputHasFocus) {
				// When input has focus but no value, or input does have focus, open panel and focus on first item.
				event.preventDefault();

				const panel = getPanelEl(host);
				panel.focus();
				focusFirstItem(panel);
			}
		}
	},

	// NOTE: This is used only for generating documentation
	dispatches: {
		/**
		 * Dispatched on each keystroke of input.
		 * @type {{ value: string }}
		 */
		'DEVX_TYPEAHEAD#INPUT_CHANGED': {},
		/**
		 * Dispatched when dropdown panel is opened an closed.
		 * @type {{ value: boolean }}
		 */
		'DEVX_TYPEAHEAD#OPENED_SET': {},
		/**
		 * Dispatched when a result item is clicked with a result item in the payload
		 *
		 * @type {{ option: {id: string, label: string, disabled: boolean} }}
		 *
		 */
		'DEVX_TYPEAHEAD#ITEM_CLICKED': {}
	},
	behaviors: [
		{
			behavior: focusBehavior
		},
		{
			behavior: keyBindingBehavior,
			options: {
				keyBindings: {
					dismiss: ['Escape', 'Tab'],
					focus: ['ArrowDown']
				},
				handlers: {
					dismiss: (element, event, dispatch) => {
						event.stopPropagation();
						dispatch(OPENED_SET, { value: false });
					},
					focus: (host, event, dispatch) => {
						const focused = getFocusedEl(host);
						const localName = get(focused, 'localName', '');
						if (event.shiftKey) {
							dispatch(OPENED_SET, { value: false });
						} else if (localName === 'devx-input') {
							dispatch('FOCUS_KEY_BINDING', { event, host, key: event.key });
						}
					}
				}
			}
		}
	]
});
