import { createEnhancedElement } from '@servicenow/library-enhanced-element';
import { actionTypes } from '@servicenow/ui-core';
import onNextFrame from '../../tools/onNextFrame';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-icon';
import '@servicenow/now-loader';
import styles from './_devx-typeahead.scss';
import { t } from 'sn-translate';
import get from 'lodash/get';
import keyBindingBehavior from '@servicenow/behavior-key-binding';

import {
	createFitBehavior,
	setFitContent,
	setFitContainer,
	setFitOpened,
	setFitTarget
} from '@servicenow/behavior-fit';

import {
	addOutsideScrollListener,
	removeOutsideScrollListener
} from './utils/outside-scroll';

import {
	addOutsideClickListener,
	removeOutsideClickListener
} from './utils/outside-click';

import {
	focusFirstItem,
	focusItem,
	focusLastItem,
	focusNextItem,
	focusPreviousItem,
	unfocusItem
} from './utils/focus';

import { getFocusedItemEl, getPanelEl, getShadowRootHost } from './utils/elements';

import { FOCUS_ON_INPUT } from './devx-typeahead';

export const ITEM_CLICKED = 'DEVX_TYPEAHEAD_PANEL#ITEM_CLICKED';
export const OPENED_SET = 'DEVX_TYPEAHEAD_PANEL#OPENED_SET';
export const OUTSIDE_CLICKED = 'DEVX_TYPEAHEAD_PANEL#OUTSIDE_CLICKED';
export const OUTSIDE_SCROLLED = 'DEVX_TYPEAHEAD_PANEL#OUTSIDE_SCROLLED';

/**
 *
 * @param {Boolean} selected
 */
const renderCheckmark = (selected) => {
	if (selected) {
		return (
			<now-icon
				className="devx-typeahead-panel-checkmark"
				icon="check-fill"
				aria-label={t('selected')}
			/>
		);
	}
	return <div className="devx-typeahead-panel-checkmark" />;
};

/**
 *
 * @param {*} properties
 */
export const DevxTypeaheadPanelItem = ({
	selectable = true,
	count = 0,
	disabled = false,
	leftGutter,
	key,
	label,
	onClick,
	onFocus,
	selected,
	setsize = 1,
	value
}) => {
	let ariaSelected = 'false';
	if (selectable || disabled) {
		ariaSelected = undefined;
	} else if (selected) {
		ariaSelected = 'true';
	}

	return (
		<li
			aria-selected={ariaSelected}
			aria-disabled={disabled ? 'true' : undefined}
			aria-setsize={setsize}
			class={{
				'devx-typeahead-panel-item': true,
				'is-selectable': selectable,
				'is-disabled': disabled
			}}
			on-focus={onFocus}
			id={key}
			on-click={!selectable || disabled ? undefined : onClick}
			on-mouseover={(ev) => {
				if (selectable) {
					const host = getShadowRootHost(ev.currentTarget);
					focusItem(host, ev.currentTarget);
				}
			}}
			on-mouseout={(ev) => {
				if (selectable) {
					const host = getShadowRootHost(ev.currentTarget);
					unfocusItem(host, ev.currentTarget);
				}
			}}
			role="option"
			tabindex="-1"
			value={value}>
			{selectable ? renderCheckmark(selected) : null}
			{leftGutter != null ? leftGutter : null}
			<span className="devx-typeahead-panel-line-height-crop">
				<span className="now-align">
					<span
						className="devx-typeahead-panel-label"
						on-mouseover={({ target }) => {
							if (target.offsetWidth < target.scrollWidth) {
								target.title = label;
							}
						}}>
						{label}
					</span>
					{count ? `\u00A0(${count})` : ''}
				</span>
			</span>
		</li>
	);
};

/**
 *
 * @param {*} state
 * @param {*} dispatch
 */
const DevxTypeaheadPanel = (state, { dispatch }) => {
	const { componentId } = state;
	const { compact, hasError, opened, items, container } = state.properties;
	const { fit } = state.behaviors;

	// FIXME - this is a hack!!!
	if (!compact) {
		fit.style.top = `${parseInt(fit.style.top) - 16}px`;
	}

	if (!opened) {
		return;
	}

	// TODO: this is a workaround until behavior-fit is replaced with now-popover.
	// It makes the panel visible only once the fit behavior has set the opened styles.
	if (fit.style.width) {
		fit.style.opacity = '1';
	}

	return (
		<div
			className="devx-typeahead-panel"
			style={fit.style}
			hook={{
				destroy: () => setFitContent(dispatch, undefined),
				insert: ({ elm }) => {
					setFitContent(dispatch, elm);
					if (container) {
						setFitContainer(dispatch, container);
					}
				}
			}}
			id={`panel-${componentId}`}>
			<ul
				class={{ 'devx-typeahead-panel-list': true, '-has-error': hasError }}
				tabindex={opened ? '0' : '-1'}
				role="listbox">
				{items}
			</ul>
		</div>
	);
};

const propChangeLookup = {
	opened: (coeffects) => {
		const {
			action: {
				payload: { value }
			},
			dispatch,
			state: { compHost, componentId }
		} = coeffects;

		setFitOpened(dispatch, value);

		if (value) {
			// TEMP FIX as the fit behavior currently isn't setting visibility
			onNextFrame(() => {
				const panelEl = getPanelEl(compHost);
				if (panelEl) {
					panelEl.style.opacity = '0';
					panelEl.style.visibility = 'visible';
				}
			});
			addOutsideScrollListener(compHost, componentId, () => {
				const panelEl = getPanelEl(compHost);
				if (panelEl) {
					panelEl.style.visibility = 'hidden';
				}
				dispatch(OUTSIDE_SCROLLED, { value: true });
			});
			addOutsideClickListener(compHost, () => {
				dispatch(OUTSIDE_CLICKED, {
					value: false
				});
			});
		} else {
			removeOutsideScrollListener(compHost);
			removeOutsideClickListener(compHost);
		}
	},
	targetRef: (coeffects) => {
		const {
			action: {
				payload: { value }
			},
			dispatch
		} = coeffects;
		setFitTarget(dispatch, value);
	},
	default() {}
};

const getKey = (key) => {
	switch (key) {
		case 'Right':
			return 'ArrowRight';
		case 'Left':
			return 'ArrowLeft';
		case 'Up':
			return 'ArrowUp';
		case 'Down':
			return 'ArrowDown';
		case 'Esc':
			return 'Escape';
		case 'Spacebar':
			return ' ';
		case 'Del':
			return 'Delete';
		default:
			return key;
	}
};
createEnhancedElement('devx-typeahead-panel', {
	properties: {
		compact: { default: false },
		hasError: { default: false },
		opened: { default: false, manageable: true },
		items: { default: [], cast: 'array' },
		targetRef: {},
		container: { default: null }
	},
	styles,
	renderer: {
		type: snabbdom,
		view: DevxTypeaheadPanel
	},
	onDisconnect(host) {
		removeOutsideScrollListener(host);
	},
	actionHandlers: {
		[actionTypes.COMPONENT_PROPERTY_CHANGED]: (coeffects) => {
			const { action } = coeffects;
			const { name } = action.payload;
			get(propChangeLookup, name, () => {})(coeffects);
		},
		[actionTypes.COMPONENT_DOM_READY]: (coeffects) => {
			const { action, updateState } = coeffects;
			const { host } = action.payload;
			// We store the host for the fit behavior to consume
			updateState({ compHost: host });
		}
	},
	behaviors: [
		{
			behavior: createFitBehavior({
				offset: [-25, 0],
				position: {
					public: true,
					default: [
						'bottom-start top-start',
						'top-start bottom-start',
						'bottom-end top-end',
						'top-end bottom-end'
					]
				},
				constrain: {
					public: true,
					default: {
						maxHeight: 300,
						maxWidth: 580,
						minHeight: 300,
						minWidth: 'target',
						width: 'target'
					}
				},
				canScrollVertical: {
					default: true
				}
			})
		},
		{
			behavior: keyBindingBehavior,
			options: {
				keyBindings: {
					moveFocus: ['Home', 'ArrowUp', 'ArrowDown', 'End'],
					dismiss: ['Escape', 'Tab'],
					select: [' ', 'Enter']
				},
				handlers: {
					moveFocus: (host, event) => {
						event.preventDefault();

						const key = getKey(event.key);

						switch (key) {
							case 'Home':
								focusFirstItem(host);
								break;
							case 'ArrowDown':
								focusNextItem(host);
								break;
							case 'ArrowUp':
								focusPreviousItem(host);
								break;
							case 'End':
								focusLastItem(host);
								break;
							default:
								return null;
						}
					},
					dismiss: (host, event, dispatch) => {
						event.preventDefault();
						dispatch(OPENED_SET, { value: false });
						dispatch(FOCUS_ON_INPUT);
					},
					select: (host, event) => {
						event.preventDefault();
						event.stopPropagation();
						const itemEl = getFocusedItemEl(host);
						if (itemEl && !itemEl.hasAttribute('aria-disabled')) {
							itemEl.click();
						}
					}
				}
			}
		}
	]
});
