import { createEnhancedElement } from '@servicenow/library-enhanced-element';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import styles from './_devx-multiselect.scss';
import '@servicenow/now-pill';
import '@servicenow/now-icon';
import { focusFirstPanelItem } from './devx-multiselect-panel';
import {
	fitBehavior,
	setFitOpened,
	setFitTarget,
	setFitContent
} from '@servicenow/behavior-fit';
import keyBindingBehavior from '@servicenow/behavior-key-binding';
import { t } from 'sn-translate';

const filterItemsBySimpleMatch = ({ items, itemDisplayKeyName }, filter) => {
	if (!filter) {
		return items;
	}
	const filterBase = filter.toLowerCase();
	return items.filter(
		(item) => item[itemDisplayKeyName].toLowerCase().indexOf(filterBase) > -1
	);
};

const setDropdownOpen = (dispatch, value) => {
	setFitOpened(dispatch, value);
	dispatch('DEVX_MULTISELECT#OPENED_SET', { value });
};

const pillDismissHandler = ({ dispatch, state, action }) => {
	const { selectedItems, items, itemIdKeyName } = state.properties;
	const newSelectedItems = selectedItems.filter(
		(item) => item[itemIdKeyName] !== action.payload.item[itemIdKeyName]
	);

	dispatch('PROPERTIES_SET', {
		selectedItems: newSelectedItems,
		items: [...items, action.payload.item]
	});

	dispatch('DEVX_MULTISELECT_PANEL#SELECTED_ITEMS_SET', {
		value: newSelectedItems
	});

	// When deleting the last item, focus should return the to input.
	if (selectedItems.length === 1) {
		state.inputRef.focus();
	}
};

// eslint-disable-next-line no-unused-vars
const MultiSelectInput = ({
	dispatch,
	inputRef,
	selectedItems,
	value,
	size,
	placeholder,
	disabled,
	itemDisplayKeyName,
	errorMsg,
	label,
	opened
}) => {
	const sizeClass = '-' + size || '-md';
	const role = 'combobox';

	return (
		<Fragment>
			<div
				className={'multi-select-wrapper ' + sizeClass}
				on-click={(e) => {
					inputRef && inputRef.focus();
					e.stopPropagation();
					e.preventDefault();
					setDropdownOpen(dispatch, !opened);
				}}>
				<div
					className="now-a11y-label"
					aria-live="assertive"
					aria-atomic="true"
					aria-relevant="all">
					{t('Selected options are: ')}
					{selectedItems.length
						? selectedItems.map((item) => item[itemDisplayKeyName]).join(', ')
						: t('none')}
				</div>
				<div
					class={{
						'multi-select-container': true,
						'-show-error': errorMsg,
						'-disabled': disabled
					}}
					hook-insert={({ elm }) => setFitTarget(dispatch, elm)}
					hook-remove={() => setFitTarget(dispatch, null)}>
					{selectedItems.map((item) => {
						return (
							<now-pill
								className="multiselect-pill"
								size="sm"
								label={item[itemDisplayKeyName]}
								canDismiss={true}
								append-to-payload={{ item }}
								manageSelected
								selected={false}
							/>
						);
					})}
					<input
						disabled={disabled}
						role={role}
						aria-label={label}
						title={errorMsg ? errorMsg : ''}
						aria-invalid={errorMsg ? 'true' : 'false'}
						placeholder={!selectedItems.length && placeholder ? placeholder : ''}
						value={value}
						hook-insert={({ elm: inputRef }) =>
							dispatch('DEVX_MULTISELECT#SET_INPUTREF', inputRef)
						}
						on-input={(evt) => {
							dispatch('DEVX_MULTISELECT#INPUT_VALUE_SET', {
								value: evt.target.value
							});
						}}
						on-blur={(evt) => {
							// Dispatch an action with the current input value in the payload,
							// so that error messages can be set/passed down from parent components.
							dispatch('DEVX_MULTISELECT#INPUT_BLUR', {
								currentInputValue: evt.target.value
							});
						}}
					/>
				</div>
				<now-icon
					class={{ 'caret-icon': true, disabled: disabled }}
					icon="caret-down-fill"
				/>
			</div>
		</Fragment>
	);
};

const GacMultiSelect = (state, { dispatch }) => {
	const {
		items,
		selectedItems,
		placeholder,
		disabled,
		size,
		filter,
		label,
		itemIdKeyName,
		itemDisplayKeyName,
		errorMsg
	} = state.properties;
	const { fit } = state.behaviors;

	const filteredItems = filterItemsBySimpleMatch(
		{ items, selectedItems, itemDisplayKeyName },
		filter
	);

	return (
		<Fragment>
			<MultiSelectInput
				inputRef={state.inputRef}
				selectedItems={selectedItems}
				label={label}
				value={filter}
				disabled={disabled}
				dispatch={dispatch}
				size={size}
				placeholder={placeholder}
				itemDisplayKeyName={itemDisplayKeyName}
				errorMsg={errorMsg}
				opened={fit.opened}
			/>
			<devx-multiselect-panel
				hidden={!filteredItems.length}
				tabindex={fit.opened ? '0' : '-1'}
				items={filteredItems}
				selected-items={selectedItems}
				opened={fit.opened}
				itemIdKeyName={itemIdKeyName}
				itemDisplayKeyName={itemDisplayKeyName}
				hook-insert={({ elm }) => setFitContent(dispatch, elm)}
				hook-update={(vnode) => {
					const oldSelectedItems = vnode.data.props.selectedItems;

					// As new items are selected, this ensured the panel is aligned
					// with the new height of the input.
					if (oldSelectedItems.length !== selectedItems.length) {
						setFitContent(dispatch, vnode.elm);
					}

					// The panel shouldn't be focused when typing. Only on ArrowDown
					if (state.openedReason === 'ArrowDown') {
						focusFirstPanelItem(vnode.elm);
					}
				}}
				hook-remove={() => setFitContent(dispatch, null)}
				style={fit.style}
			/>
		</Fragment>
	);
};

/**
 * Multiselect
 *
 * ```jsx
 * <devx-multiselect
 *     label={label}
 *     placeholder={placeholder}
 *     items={sortedOptions}
 *     selectedItems={filteredSelectedItems}
 *     size={size}
 *     disabled={disabled}
 *     itemDisplayKeyName={itemDisplayKeyName}
 *     errorMsg={errorMsg}
 * />
 * ```
 *
 * @seismicElement devx-multiselect
 * @summary A component that allows the user to select mutiple options from a
 * 			dropdown list of options.
 */
createEnhancedElement('devx-multiselect', {
	properties: {
		/**
		 * The keyname of the item value
		 * @type {string}
		 */
		itemIdKeyName: { default: 'value' },

		/**
		 * The keyname for the item display label
		 * @type {string}
		 */
		itemDisplayKeyName: { default: 'text' },

		/**
		 * Label to be displayed above the input field.
		 * @type {string}
		 */
		label: {},
		/**
		 * An array of item objects describing each item in the dropdown.
		 * Each object must have a unique id and label.
		 * @type {{id: (string|number), label: string}[]}
		 */
		items: { default: [], cast: 'array' },
		/**
		 * An array of ids representing each selected item.
		 * @type {(string|number)[]}
		 */
		selectedItems: { default: [], cast: 'array' },
		/**
		 * Automatically updated when the user opens or closes the dropdown.
		 * Manage the property to control from the app.
		 * @type {boolean}
		 */
		opened: { default: false, manageable: true },
		/**
		 * Fallback label used if no items are selected.
		 * @type {string}
		 */
		placeholder: {},
		/**
		 * Enable to show the dropdown as visually disabled and to stop the dropdown
		 * from opening when the user interacts with it.
		 * @type {boolean}
		 */
		disabled: { default: false },
		/**
		 * Optional icon to include in the trigger button.
		 * @type {('sm'|'md'|'lg'|'fullwidth')}
		 */
		size: { default: 'md', reflect: true },
		/**
		 * Filter input text
		 * @type {string}
		 */
		filter: { default: '', manageable: true },
		/**
		 * Number of characters the user must type before the dropdown appears.
		 * @type {number}
		 */
		minFilterLength: { default: 0 },
		/**
		 * Sets error styles
		 * @type {boolean}
		 */
		errorMsg: { default: null }
	},
	view: GacMultiSelect,
	styles,
	actionHandlers: {
		'NOW_PILL#DISMISSED': (coeffects) => {
			pillDismissHandler(coeffects);
		},
		'DEVX_MULTISELECT#SET_INPUTREF': ({ updateState, action: { payload } }) => {
			updateState({
				inputRef: payload
			});
		},
		'DEVX_MULTISELECT_PANEL#OPENED_SET': ({ action, dispatch }) => {
			setFitOpened(dispatch, action.payload.value);
			dispatch('DEVX_MULTISELECT#OPENED_SET', { value: action.payload.value });
		},
		'DEVX_MULTISELECT_PANEL#SELECTED': ({ action, dispatch, state }) => {
			const { selectedItems, itemIdKeyName, items } = state.properties;
			const { item } = action.payload;

			const newSelectedItems = [...selectedItems, item];

			dispatch('PROPERTIES_SET', {
				selectedItems: newSelectedItems,
				items: items.filter((i) => {
					return i[itemIdKeyName] !== item[itemIdKeyName];
				}),
				filter: null
			});

			dispatch('DEVX_MULTISELECT_PANEL#SELECTED_ITEMS_SET', {
				value: newSelectedItems
			});

			// This is needed because of a race condition
			// with closing the panel. This race condition is slightly better.
			// Hope and pray that this finishes before the focus event
			// or else the panel will steal focus.
			dispatch('DEVX_MULTISELECT#CLEAR_OPEN_REASON');
			dispatch('DEVX_MULTISELECT_PANEL#FOCUS_INPUT');
		},
		'DEVX_MULTISELECT#CLEAR_OPEN_REASON': ({ updateState }) => {
			updateState({
				openedReason: ''
			});
		},
		'DEVX_MULTISELECT_PANEL#FOCUS_INPUT': ({ state }) => state.inputRef.focus(),
		'DEVX_MULTISELECT#INPUT_VALUE_SET': ({ action, dispatch, state }) => {
			const { value } = action.payload;
			const { minFilterLength } = state.properties;
			const {
				fit: { opened }
			} = state.behaviors;

			if (value && value.length >= minFilterLength && !opened) {
				setDropdownOpen(dispatch, true);
			}

			dispatch('DEVX_MULTISELECT#FILTER_SET', {
				value: action.payload.value
			});
		},
		'DEVX_MULTISELECT#BACKSPACE': ({ dispatch, state }) => {
			const { selectedItems, filter, items } = state.properties;

			if (!filter && selectedItems.length) {
				const newSelectedItems = selectedItems.slice(0, -1);
				dispatch('PROPERTIES_SET', {
					selectedItems: newSelectedItems,
					items: [...items, selectedItems[selectedItems.length - 1]]
				});
				setDropdownOpen(dispatch, false);

				dispatch('DEVX_MULTISELECT_PANEL#SELECTED_ITEMS_SET', {
					value: newSelectedItems
				});
			}
		},
		'DEVX_MULTISELECT#ARROW_DOWN': ({ dispatch, updateState }) => {
			setDropdownOpen(dispatch, true);
			updateState({
				openedReason: 'ArrowDown'
			});
		}
	},
	initialState: {
		openedReason: '',
		inputRef: null
	},
	behaviors: [
		{
			behavior: fitBehavior,
			options: {
				position: [
					'bottom-start top-start',
					'top-start bottom-start',
					'bottom-end top-end',
					'top-end bottom-end'
				],
				constrain: {
					minHeight: 300,
					minWidth: 'target',
					width: 'target',
					maxWidth: 580,
					maxHeight: 300
				}
			}
		},
		{
			behavior: keyBindingBehavior,
			options: {
				keyBindings: {
					onArrowKeyPress: 'ArrowDown',
					onEscapeKeyPress: 'Escape',
					onBackspaceKeyPress: 'Backspace',
					onTabKeyPress: 'Tab'
				},
				handlers: {
					onArrowKeyPress: function(host, event, dispatch) {
						// FIXME: Work around to stop event bubbled from panel
						if (!event.defaultPrevented) {
							dispatch('DEVX_MULTISELECT#ARROW_DOWN');
						}
					},
					onEscapeKeyPress: function(host, event, dispatch) {
						// FIXME: Work around to stop event bubbled from panel
						if (!event.defaultPrevented) {
							setDropdownOpen(dispatch, false);
						}
					},
					// If the input is empty when backspacing, remove the last
					// selected item.
					onBackspaceKeyPress: function(host, event, dispatch) {
						dispatch('DEVX_MULTISELECT#BACKSPACE');
					},
					// If the user shift+tabs out of the input, close the dropdown.
					onTabKeyPress: function(host, event, dispatch) {
						event.stopPropagation();
						if (event.shiftKey) {
							dispatch('DEVX_MULTISELECT_PANEL#OPENED_SET', { value: false });
						}
					}
				}
			}
		}
	]
});
