import { findTabbableNodes } from '@servicenow/behavior-key-binding';

export const START_ID = 'DEVX_FOCUS_TRAP_START_ID';
export const END_ID = 'DEVX_FOCUS_TRAP_END_ID';

export const INPUT_TAG = 'INPUT';
export const BUTTON_TAG = 'BUTTON';
export const LINK_TAG = 'A';

/**
 * afterNextRender - helps ensure that elements are properly rendered and available when
 * calling findTabbableNodes during a (re)render
 * Admittedly hacky, but this is from the Emotive team -
 * https://code.devsnc.com/gist/david-leonard/5e791574ce6b4850bf685683da580607
 */
export const afterNextRender = () => {
	return new Promise((resolve) =>
		setTimeout(() => window.requestAnimationFrame(resolve), 0)
	);
};

/**
 * getTabbableNodes - just the findTabbableNodes method with afterNextRender
 */
export const getTabbableNodes = async (parentNode) => {
	await afterNextRender();
	return findTabbableNodes(parentNode);
};

/**
 * focusFirstElem - focuses the first tabbableNode returned, using getTabbableNodes
 * so this is _relatively_ safe to use in an COMPONENT_BOOTSTRAPPED/COMPONENT_CONNECTED/COMPONENT_PROPERTY_CHANGED.
 * The first argument is the parentNode to traverse, and the second optional argument
 * is the tagName of the kind element to focus
 */
export const focusFirstElem = async (parentNode, elemTag = null) => {
	const tabbableNodes = await getTabbableNodes(parentNode);

	// Safeguards use with devx-focus-trap, filters out the start and end boundary elements
	const filteredNodes = tabbableNodes.filter((node) => node.id !== (START_ID || END_ID));

	if (!elemTag) {
		filteredNodes[0] && filteredNodes[0].focus();
	} else {
		const foundNode = filteredNodes.find((node) => node.tagName === elemTag);
		foundNode && foundNode.focus();
	}
};

// curriedFocusFirstElem
export const curriedFocusFirstElem = (elemTag) => (parentNode) =>
	focusFirstElem(parentNode, elemTag);

// A couple extra helpers for focusing the first of a specific kind of element
export const focusFirstInput = curriedFocusFirstElem(INPUT_TAG);
export const focusFirstButton = curriedFocusFirstElem(BUTTON_TAG);
export const focusFirstLink = curriedFocusFirstElem(LINK_TAG);
