import isEqual from 'lodash/isEqual';

export const hasPath = (list, path) => list.findIndex((p) => isEqual(p, path)) > -1;

export const removePath = (list, path) => list.filter((p) => !isEqual(p, path));

export const addPath = (list, path) => [...list, path];

export const togglePath = (list, path) =>
	hasPath(list, path) ? removePath(list, path) : addPath(list, path);

export const makeRedispatchEffect = (newActionType) => ({ action, dispatch }) =>
	dispatch(newActionType, { ...(action.payload || {}) });

export const endOfFrame = () => new Promise((resolve) => setTimeout(resolve, 0));

export const extensionSplit = (value) => {
	if (value.indexOf('-') === -1) {
		return { ['-' + value]: value };
	}
	return value.split('-').reduce((acc, v) => {
		acc['-' + v] = v;
		return acc;
	}, {});
};
