import {
	getListboxEl,
	getItemEls,
	getFocusedItemEl,
	getFocusedItemIndex,
	getSelectedItemEls,
	isPanelList
} from './elements';
import { endOfFrame } from './helpers.js';

/**
 *
 * @param {HTMLElement} el
 */
export const getShadowRootHost = (el) => {
	if (el.parentElement) {
		return getShadowRootHost(el.parentElement);
	}
	if (el.parentNode && el.parentNode instanceof DocumentFragment) {
		return el.parentNode.host;
	}
	return null;
};

/**
 * Focuses a given item in the dropdown panel and scrolls it into view, if necessary.
 * @param {HTMLElement} host
 * @param {HTMLElement} targetElement
 */
export const focusItem = (host, targetElement) => {
	const listbox = getListboxEl(host);

	targetElement.focus({
		preventScroll: true
	});

	const overflow = listbox.scrollHeight > listbox.clientHeight;
	if (overflow) {
		const scrollBottom = listbox.scrollTop + listbox.clientHeight;
		const itemBottom = targetElement.offsetTop + targetElement.offsetHeight;
		if (itemBottom > scrollBottom) {
			// Scroll down to make item visible
			listbox.scrollTop = itemBottom - listbox.clientHeight;
		} else if (targetElement.offsetTop < listbox.scrollTop) {
			// Scroll up to make item visible
			listbox.scrollTop = targetElement.offsetTop;
		}
	}
};

/**
 * Removes focus on a given item.
 * @param {HTMLElement} host
 */
export const unfocusItem = (host) => {
	const listbox = getListboxEl(host);
	listbox.focus();
};

/**
 * Focuses the first enabled item (or first selected item) when the dropdown panel is opened.
 * @param {HTMLElement} host
 */
export const focusOnOpen = async (host) => {
	await endOfFrame();
	const listbox = getListboxEl(host);
	listbox.focus();
	const firstFocusItem = getSelectedItemEls(host)[0] || getItemEls(host)[0];
	if (firstFocusItem) {
		focusItem(host, firstFocusItem);
	}
};

/**
 * Moves focus to previous enabled element (if currently focused) or first enabled element.
 * @param {HTMLElement} host
 */
export const focusPreviousItem = (host) => {
	const lastFocusItem = getFocusedItemEl(host);
	if (!lastFocusItem || isPanelList(lastFocusItem)) {
		focusFirstItem(host);
		return;
	}
	const lastFocusIndex = getFocusedItemIndex(host);
	const nextFocusItem = getItemEls(host)[lastFocusIndex - 1];
	if (nextFocusItem) {
		focusItem(host, nextFocusItem);
	}
};

/**
 * Moves focus to next enabled element (if currently focused) or first enabled element.
 * @param {HTMLElement} host
 */
export const focusNextItem = (host) => {
	const lastFocusItem = getFocusedItemEl(host);
	if (!lastFocusItem || isPanelList(lastFocusItem)) {
		focusFirstItem(host);
		return;
	}
	const lastFocusIndex = getFocusedItemIndex(host);
	const nextFocusItem = getItemEls(host)[lastFocusIndex + 1];
	if (nextFocusItem) {
		focusItem(host, nextFocusItem);
	}
};

/**
 * Focuses the first enabled item in the dropdown panel.
 * @param {HTMLElement} host
 */
export const focusFirstItem = (host) => {
	const nextFocusItem = getItemEls(host)[0];
	if (nextFocusItem) {
		focusItem(host, nextFocusItem);
	}
};

/**
 * Focuses the last enabled item in the dropdown panel.
 * @param {HTMLElement} host
 */
export const focusLastItem = (host) => {
	const nextFocusItem = getItemEls(host).slice(-1)[0];
	if (nextFocusItem) {
		focusItem(host, nextFocusItem);
	}
};
