/**
 * Map of possible modifier boolean values on the KeyboardEvent object
 * to their text value derived from KeyboardEvent.key
 */
const MODIFIER_MAP = {
	altKey: 'Alt',
	ctrlKey: 'Control',
	metaKey: 'Meta',
	shiftKey: 'Shift'
};

/**
 * Returns a standardized key name based on the modifiers held at event dispatch.
 * Each key in the string is capitalized and should be in line with the values present in `KeyboardEvent#key`.
 * Modifiers will be present in alphabetical order, and the last key pressed is ALWAYS last, even when that key is a modifier.
 * @example
 * parseKeyName(event) // AltControlMetaShiftTab
 * @param {KeyboardEvent} event
 * @returns {string}
 */
function parseKeyName(event) {
	const modifiers = [];
	const key = event.key === ' ' ? 'Space' : event.key;

	for (const [check, value] of Object.entries(MODIFIER_MAP)) {
		if (key !== value && event[check]) {
			modifiers.push(value);
		}
	}

	// The last key pressed, even a modifier, will ALWAYS be last.
	// Otherwise modifiers will be alphabetized
	modifiers.sort().push(key);
	return modifiers.join('');
}

export default parseKeyName;
