import { createCustomElement, actionTypes } from '@servicenow/ui-core';
import styles from './devx-iframe.scss';
import isEqual from 'lodash/isEqual';

const { COMPONENT_CONNECTED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

const DEVX_IFRAME_UPDATE_CHILD_FRAME = 'DEVX_IFRAME#UPDATE_CHILD_FRAME';

/**
 * The format of the `data` property on `MessageEvent`s used in transferring data between the iframe
 * and the seismic app that loaded it.
 *
 * @typedef MessageData
 * @property {string} actionType The name of the action to dispatch in parent app, or 'DEVX_IFRAME#UPDATE_CHILD_FRAME' to signal child frame update
 * @property {*} payload The data that will act as the payload for the proxied action
 */

/**
 * Enforces the same-origin policy before the iframe is loaded
 * @param {string} src The `src` property for the iframe
 */
function validateSource(src) {
	if (typeof src !== 'string') {
		throw new TypeError('[devx-iframe] The provided source url must be a string');
	}

	const { origin } = window.location;
	const url = new URL(src, origin);
	if (url.origin !== origin) {
		throw new Error(
			`[devx-iframe] The provided source '${src}' should contain same origin: '${origin}'`
		);
	}
}

function createMessageHandler(dispatch) {
	/**
	 * @param {MessageEvent} event
	 */
	function messageHandler(event) {
		/** @type {MessageData} */
		const data = event.data;
		const { actionType, payload } = data;

		if (
			event.origin === window.location.origin &&
			typeof actionType === 'string' &&
			actionType !== DEVX_IFRAME_UPDATE_CHILD_FRAME
		) {
			dispatch(actionType, payload);
		}
	}

	return messageHandler;
}

/**
 * A helper function to enforce the `postMessage` API
 * @param {window} contentWindow The iframe's content window
 * @param {*} payload The `payload` property of MessageData
 */
function postData(contentWindow, payload) {
	/** @type {MessageData} */
	const data = {
		actionType: DEVX_IFRAME_UPDATE_CHILD_FRAME,
		payload
	};
	contentWindow.postMessage(data, window.location.origin);
}

/**
 * A component that wraps an iframe and dispatches actions sent from it while also passing in data from props.
 *
 * When the iframe wants to dispatch an action to the parent app, you must send a message with the data in the `MessageData` format.
 * Conversly, the iframe must add a `MessageEvent` listener that is scoped for the `actionType: 'DEVX_IFRAME#UPDATE_CHILD_FRAME'`
 *
 * @example
 * // dispatching action in iframe
 * window.postMessage({
 * 	actionType: 'SOME_COOL_ACTION',
 * 	payload: {...data}
 * }, window.location.origin)
 *
 * // receiving data from host app
 * window.addEventListener('message', (event) => {
 * 	if (
 * 		event.origin === window.location.origin &&
 * 		event.data.actionType === 'DEVX_IFRAME#UPDATE_CHILD_FRAME'
 * 	) {
 * 		// Awesome code happens here
 * 	}
 * })
 *
 * @param {object} state
 * @param {function} dispatch
 */
function DevxIframe(state, { dispatch }) {
	const { src, title, data, redirectFocusTo } = state.properties;

	return (
		<iframe
			className="devx-iframe"
			src={src}
			title={title}
			aria-label={title}
			on-focus={(event) => {
				if (!redirectFocusTo) {
					return;
				}

				let focusElement = event.target.contentDocument.querySelector(redirectFocusTo);

				// Special case: if the element we're focusing an inner iframe, focus the body.
				if (focusElement && focusElement.nodeName === 'IFRAME') {
					focusElement = focusElement.contentDocument.body;
				}

				if (focusElement) {
					focusElement.focus();
				}
			}}
			on-load={(event) => {
				/** @type {{contentWindow: window}} */
				const { contentWindow } = event.target;
				const messageHandler = createMessageHandler(dispatch);
				contentWindow.addEventListener('message', messageHandler, false);

				if (data) {
					postData(contentWindow, data);
				}
			}}
		/>
	);
}

createCustomElement('devx-iframe', {
	view: DevxIframe,

	properties: {
		src: {
			default: null,
			reflect: true
		},

		title: {
			default: null,
			reflect: true
		},

		data: {
			default: null
		},

		/*
		A CSS selector for an element inside of the iframe to focus, if the iframe
		receives focus.  This can be used to fix accessibility / tab navigation
		issues where we want to focus an element inside of the iframe and not the
		the iframe itself.
		*/
		redirectFocusTo: {
			default: null
		}
	},

	actionHandlers: {
		[COMPONENT_CONNECTED]: ({ properties }) => {
			validateSource(properties.src);
		},

		[COMPONENT_PROPERTY_CHANGED]: ({ action }) => {
			const { host, value, name, previousValue } = action.payload;

			if (name === 'data' && !isEqual(value, previousValue)) {
				/** @type {{contentWindow: window}} */
				const { contentWindow } = host.shadowRoot.querySelector('.devx-iframe');
				postData(contentWindow, value);
			}

			if (name === 'src') {
				validateSource(value);
			}
		}
	},

	styles
});
