export { default as DevxKeyFocusBehavior, createFocusRef } from './DevxKeyFocusBehavior';

/**
 * Calls focus on a target ref defined by `createFocusRef`
 * @param {function} dispatch The `dispatch` function in context of the host component
 * @param {string} targetId The ID of a target ref created by `createFocusRef`
 */
export function focusTargetId(dispatch, targetId) {
	dispatch('DEVX_KEY_FOCUS_BEHAVIOR#FOCUS_TARGET_ID', { targetId });
}
