const hostToClickListener = new WeakMap();

/**
 * Adds click event listener to the document. If clicks originate outside
 * of the host element, it triggers the callback function.
 * @param {HTMLElement} host
 * @param {function} callback
 */
export const addOutsideClickListener = (host, callback) => {
	const listener = (e) => {
		const path = e.composedPath ? e.composedPath() : e.path;
		const hostInPath = path.indexOf(host) !== -1;
		if (!hostInPath) {
			if (callback && typeof callback === 'function') {
				callback(host);
			}
		}
	};

	// Adding `capture` prioritizes the event handler to be run before all other click handlers
	document.addEventListener('click', listener, { capture: true });
	hostToClickListener.set(host, listener);
};

/**
 * Cleans up the outside click event listener created for the given host element.
 * @param {HTMLElement} host
 */
export const removeOutsideClickListener = (host) => {
	const listener = hostToClickListener.get(host);
	if (listener) {
		document.removeEventListener('click', listener, { capture: true });
		hostToClickListener.delete(host);
	}
};
