import parseKeyName from '../../tools/parseKeyName';
import onNextFrame from '../../tools/onNextFrame';

/**
 * @typedef FocusRefMeta
 * @property {string} focusId
 * @property {object} keyMap
 */

/**
 * @type {Map<HTMLElement, FocusRefMeta>}
 */
export const FOCUS_REF_CACHE = new Map();

/**
 * @param {string} focusId
 * @returns {HTMLElement}
 */
export function findElementByFocusId(focusId) {
	for (const [element, { focusId: metaId }] of [...FOCUS_REF_CACHE.entries()]) {
		let result = element;

		// Lazy garbage collection since it seems vdom doesn't set a ref's `current` to null
		// ... so far as I can tell anyway
		if (!element.isConnected) {
			FOCUS_REF_CACHE.delete(element);
			result = undefined;
		}

		if (metaId === focusId) {
			return result;
		}
	}

	return undefined;
}

/**
 * Creates a ref to be assigned on a snabbdom element.
 * The id is required and marks the element as a focus target via this behavior.
 * The keyMap is optional and is defined where the keys are the values of `KeyboardEvent.key`,
 * 	and values are other focus target ids registered in this manner.
 *
 * Note:
 * 1. For the keybindings to work, the element itself must be focusable.
 * 2. The element must be focused when the key is pressed for the bindings to take effect.
 * 3. vdom uses rendered HTML to rectify its diff, therefore similar HTML in similar positions
 * 		have a tendency to inherit their refs... be careful
 *
 * @example
 * <button ref={createFocusRef('button-anchor', { Enter: 'input-anchor' })}>
 * 	Push to focus input
 * </button>
 *
 * <input ref={createFocusRef('input-anchor', { Escape: 'button-anchor' })} />
 *
 * @param {string} focusId Anchor id of the current element
 * @param {object} [keyMap] Object configuring the key code - target id of destination element
 */
export function createFocusRef(focusId, keyMap = {}) {
	const ref = {
		get current() {
			return findElementByFocusId(focusId);
		},

		set current(element) {
			if (element instanceof HTMLElement) {
				element.dataset.focusId = focusId;

				/** @type {FocusRefMeta} */
				const meta = { focusId, keyMap };
				FOCUS_REF_CACHE.set(element, meta);
			} else {
				const cachedElement = findElementByFocusId(focusId);
				FOCUS_REF_CACHE.delete(cachedElement);
			}
		}
	};

	Object.seal(ref);
	return ref;
}

/**
 * @param {HTMLElement|DocumentFragment} path Array of elements (or shadow doms) traversed by the event
 * @returns {HTMLElement}
 */
export function findClosestRefElement(path = []) {
	return path.find((element) => FOCUS_REF_CACHE.has(element));
}

/**
 * @param {HTMLElement} refElement
 * @param {KeyboardEvent} event
 * @returns {string}
 */
export function findTargetId(refElement, event) {
	if (refElement) {
		const keyName = parseKeyName(event);
		const meta = FOCUS_REF_CACHE.get(refElement);
		return meta.keyMap[keyName];
	}

	return undefined;
}

/**
 * @param {string} targetId
 * @returns {boolean}
 */
export function focusByTargetId(targetId) {
	if (targetId) {
		const targetElement = findElementByFocusId(targetId);

		if (targetElement) {
			onNextFrame(() => {
				targetElement.focus();
			});
			return true;
		}
	}

	return false;
}
const getPath = (event) => {
	/**
	 * https://developer.mozilla.org/en-US/docs/Web/API/Event/composedPath
	 * composedPath is a web standard but only supported in Safari, Chrome, and Firefox.
	 * IE11 and Edge (non-chromium) are not supported so not a concern.
	 * We attempt to default to `path`. Which is a non-standard API.
	 */
	if (!event.composedPath) {
		return event.path || [];
	}

	return event.composedPath();
};
/**
 * Attach on a parent component that houses a focus ref with a `keyMap` configured.
 * dispatches DEVX_KEY_FOCUS_BEHAVIOR#TARGET_REF_FOCUSED
 */
const DevxKeyFocusBehavior = {
	name: 'DevxKeyFocusBehavior',

	dispatches: {
		/**
		 * @typedef TargetRefFocusedPayload
		 * @property {string} targetId
		 */
		'DEVX_KEY_FOCUS_BEHAVIOR#TARGET_REF_FOCUSED': {}
	},

	actionHandlers: {
		'DEVX_KEY_FOCUS_BEHAVIOR#FOCUS_TARGET_ID': ({ action, dispatch }) => {
			const { targetId } = action.payload;

			if (focusByTargetId(targetId)) {
				action.preventDefault();
				action.stopPropagation();

				/** @type {TargetRefFocusedPayload} */
				const payload = { targetId };
				dispatch('DEVX_KEY_FOCUS_BEHAVIOR#TARGET_REF_FOCUSED', payload);
			}
		}
	},

	eventHandlers: [
		{
			events: ['keydown'],
			effect({ action, dispatch }) {
				/** @type {KeyboardEvent} */
				const event = action.payload.event;
				const refElement = findClosestRefElement(getPath(event));
				const targetId = findTargetId(refElement, event);

				if (focusByTargetId(targetId)) {
					event.preventDefault();
					event.stopImmediatePropagation();

					/** @type {TargetRefFocusedPayload} */
					const payload = { targetId };
					dispatch('DEVX_KEY_FOCUS_BEHAVIOR#TARGET_REF_FOCUSED', payload);
				}
			}
		}
	]
};

export default DevxKeyFocusBehavior;
