import { createCustomElement } from '@servicenow/ui-core';
import overlayBehavior from '@servicenow/behavior-overlay';

import styles from './_devx-multiselect-panel.scss';
import keyBindingBehavior from '@servicenow/behavior-key-binding';
import { t } from 'sn-translate';

const includesBy = (arr, value, key) => arr.some((i) => i[key] === value);

const focusIndexIncrement = (updateState, state, totalItemCount) => {
	const focusIndex = state.focusIndex;

	if (focusIndex === null) {
		updateState({
			focusIndex: 0
		});

		return;
	}

	if (focusIndex < totalItemCount - 1) {
		updateState({
			focusIndex: focusIndex + 1
		});
	}
};

const focusIndexDecrement = (updateState, state) => {
	const focusIndex = state.focusIndex;
	if (focusIndex > 0) {
		updateState({
			focusIndex: focusIndex - 1
		});
	}
};

const scrollAndFocusItem = (itemEl, panelRef) => {
	itemEl.scrollIntoView({ block: 'nearest' });
	// When focus is called the element is scrolled to.
	// The scrolling isn't as accurate as the scrollIntoView
	// above and unfortunately the focus preventScroll option
	// experimental at this point in time. The timeouts ensure
	// layout has happened and then scrollTop is reset.
	setTimeout(() => {
		const yScroll = panelRef.scrollTop;
		itemEl.focus();
		setTimeout(() => {
			panelRef.scrollTop = yScroll;
		}, 0);
	}, 16);
};

// eslint-disable-next-line no-unused-vars
const DropdownPanelItem = ({ label, onClick, onFocus, index, focusIndex, panelRef }) => {
	const isFocused = index === focusIndex;
	const itemClasses = {
		'dropdown-panel-item': true,
		'-focused': isFocused
	};

	return (
		<li
			// eslint-disable-next-line
			tabindex="0"
			role="option"
			aria-selected={isFocused}
			class={itemClasses}
			on-click={onClick}
			on-focus={onFocus}
			hook={{
				update(_, newVnode) {
					if (!isFocused) {
						return;
					}

					scrollAndFocusItem(newVnode.elm, panelRef);
				}
			}}>
			<span
				className="now-dropdown-panel-item-label"
				on-mouseover={({ target }) => {
					if (target.offsetWidth < target.scrollWidth) {
						target.title = label;
					}
				}}>
				{label}
			</span>
		</li>
	);
};

export const focusFirstPanelItem = (panelRef) => {
	const items = [...panelRef.shadowRoot.querySelectorAll('li.dropdown-panel-item')];
	if (items[0]) {
		scrollAndFocusItem(items[0], panelRef);
	}
};

let panelRef = null;
/** @seismicView devx-multiselect-panel */
const EdsDropdownPanel = (state, { dispatch, updateState }) => {
	const { items, selectedItems, itemIdKeyName, itemDisplayKeyName } = state.properties;

	return (
		<ul
			class={{
				'now-dropdown-list': true
			}}
			role="listbox"
			aria-label={t('Dropdown panel containing {0} options', items.length)}
			hook-insert={({ elm }) => (panelRef = elm)}>
			{items.map((item, index) => {
				return (
					<DropdownPanelItem
						panelRef={panelRef}
						key={item[itemIdKeyName]}
						index={index}
						focusIndex={state.focusIndex}
						label={item[itemDisplayKeyName]}
						selected={includesBy(selectedItems, item[itemIdKeyName], itemIdKeyName)}
						onClick={() => {
							dispatch('DEVX_MULTISELECT_PANEL#SELECTED', { item });
						}}
						onFocus={() => {
							if (state.focusIndex !== index) {
								updateState({ focusIndex: index });
							}
						}}
					/>
				);
			})}
		</ul>
	);
};

/** @private */
createCustomElement('devx-multiselect-panel', {
	properties: {
		/**
		 * An array of filtered item objects describing each item in the dropdown.
		 * Each object must have a unique id and label.
		 * @type {{id: (string|number), label: string}[]}
		 */
		items: { default: [], cast: 'array' },

		/**
		 * An array of ids representing each selected item.
		 * @type {(string[])[]}
		 */
		selectedItems: { default: [], cast: 'array' },

		/**
		 * The keyname of the item value
		 * @type {string}
		 */
		itemIdKeyName: {},

		/**
		 * The keyname for the item display label
		 * @type {string}
		 */
		itemDisplayKeyName: {}
	},
	initialState: {
		focusIndex: null
	},
	view: EdsDropdownPanel,
	styles,
	actionHandlers: {
		'OVERLAY_BEHAVIOR#OPENED_SET': ({ action, dispatch }) => {
			dispatch('DEVX_MULTISELECT_PANEL#OPENED_SET', {
				value: action.payload.opened
			});
			dispatch('DEVX_MULTISELECT#CLEAR_OPEN_REASON');
		},
		// Parent also consumes this action
		'DEVX_MULTISELECT_PANEL#SELECTED': ({ dispatch }) => {
			dispatch('DEVX_MULTISELECT_PANEL#OPENED_SET', { value: false });
		},
		// Parent also consumes this action
		'DEVX_MULTISELECT_PANEL#OPENED_SET': ({ updateState }) => {
			updateState({
				focusIndex: null
			});
		},
		'DEVX_MULTISELECT_PANEL#ARROW_KEY_PRESS': ({ action, updateState, state }) => {
			const { host, event } = action.payload;

			switch (event.key) {
				case 'ArrowDown': {
					return focusIndexIncrement(updateState, state, host.items.length);
				}
				case 'ArrowUp':
					return focusIndexDecrement(updateState, state);
				default:
					return;
			}
		},
		'DEVX_MULTISELECT_PANEL#TAB_KEY_PRESS': ({
			action,
			updateState,
			dispatch,
			state
		}) => {
			const { host, event } = action.payload;
			const focusIndex = state.focusIndex;

			if (event.shiftKey) {
				// If the beginning of the list has been reached close the panel.
				// This prevents tabbing out of the panel and opening another
				// dropdown on top of it.
				if (focusIndex === 0 || focusIndex === null) {
					dispatch('DEVX_MULTISELECT_PANEL#FOCUS_INPUT');

					return dispatch('DEVX_MULTISELECT_PANEL#OPENED_SET', {
						value: false
					});
				} else {
					return focusIndexDecrement(updateState, state);
				}
			} else {
				// If the end of the list has been reached close the panel.
				// This prevents tabbing out of the panel and opening another
				// dropdown on top of it.
				if (focusIndex === host.items.length - 1) {
					dispatch('DEVX_MULTISELECT#CLEAR_OPEN_REASON');
					dispatch('DEVX_MULTISELECT_PANEL#FOCUS_INPUT');
					return dispatch('DEVX_MULTISELECT_PANEL#OPENED_SET', {
						value: false
					});
				} else {
					return focusIndexIncrement(updateState, state, host.items.length);
				}
			}
		},
		'DEVX_MULTISELECT_PANEL#ENTER_KEY_PRESS': ({ action, dispatch, state }) => {
			const { host } = action.payload;
			const { focusIndex } = state;

			// Protects against the case when only the panel is focused
			if (focusIndex === undefined || focusIndex === null) {
				return;
			}

			dispatch('DEVX_MULTISELECT_PANEL#SELECTED', {
				item: host.items[focusIndex]
			});
		}
	},
	behaviors: [
		{
			behavior: overlayBehavior
		},
		{
			behavior: keyBindingBehavior,
			options: {
				keyBindings: {
					onEnterKeyPress: 'Enter',
					onArrowKeyPress: ['ArrowUp', 'ArrowDown'],
					onEscapeKeyPress: 'Escape',
					onBackspaceKeyPress: 'Backspace',
					onTabKeyPress: 'Tab'
				},
				handlers: {
					onArrowKeyPress: function(host, event, dispatch) {
						event.stopPropagation();
						event.preventDefault();

						dispatch('DEVX_MULTISELECT_PANEL#ARROW_KEY_PRESS', { host, event });
					},
					onTabKeyPress: function(host, event, dispatch) {
						event.stopPropagation();
						event.preventDefault();

						dispatch('DEVX_MULTISELECT_PANEL#TAB_KEY_PRESS', { host, event });
					},
					onEscapeKeyPress: (host, event, dispatch) => {
						event.stopPropagation();
						event.preventDefault();

						dispatch('OVERLAY_BEHAVIOR#OPENED_SET', { value: false });
						dispatch('DEVX_MULTISELECT_PANEL#FOCUS_INPUT');
					},
					onEnterKeyPress: function(host, event, dispatch) {
						dispatch('DEVX_MULTISELECT_PANEL#ENTER_KEY_PRESS', { host, event });
					},
					onBackspaceKeyPress: function(host, event) {
						event.stopPropagation();
						event.preventDefault();
					}
				}
			}
		}
	]
});
