/**
 * TODO: (andrew.babbitt) To follow best practices for a11y support on tooltips,
 * the trigger should be a <button /> that renders the tooltip on mouseover or keyboard focus
 * and use aria-describedby to dictate what is read, until <now-tooltip> supports the
 * "opened" (currently readonly) property programatically, this cannot be done
 *
 * More information can be found here: http://w3c.github.io/aria-practices/#tooltip
 */

import { createCustomElement } from '@servicenow/ui-core';
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-tooltip';
import styles from './_devx-tooltip-icon.scss';
import tooltipIcon from './assets/info-circle.svg';

export const SET_TARGET_REF = 'DEVX_TOOLTIP_ICON#SET_TARGET_REF';
export const TOOLTIP_ENTER = 'DEVX_TOOLTIP_ICON#TOOLTIP_ENTER';
export const TOOLTIP_EXIT = 'DEVX_TOOLTIP_ICON#TOOLTIP_EXIT';

/**
 *
 * ```jsx
 * <devx-tooltip-icon text="tooltip text here" />
 * ```
 * @deprecated in paris
 *
 * @seismicElement devx-tooltip-icon
 * @summary An accessible component for showing tooltip text over a icon. This is a wrapper around
 * 			the now-tooltip component.
 *
 */
createCustomElement('devx-tooltip-icon', {
	view: (state, { dispatch }) => {
		const {
			targetRef,
			properties: { text, name }
		} = state;

		const setTargetRef = (ref) => {
			dispatch(SET_TARGET_REF, ref);
		};

		return (
			<Fragment>
				<img
					src={tooltipIcon}
					hook-insert={({ elm }) => setTargetRef(elm)}
					hook-remove={() => setTargetRef(null)}
					tab-index="0"
					alt=""
					/**
					 * A shortcut here would be to set the `alt` attribute to the `{text}`,
					 * instead of setting it as a specific `aria-label`. This works in Chrome
					 * and Safari, but Firefox cannot handle it correctly and treats the `alt`
					 * text as the same as the content of `now-tooltip`. The result is that it
					 * is rendered momentarily on initial page load.
					 */
					aria-label={text}
					onmouseenter={() => dispatch(TOOLTIP_ENTER, { name })}
					onmouseleave={() => dispatch(TOOLTIP_EXIT, { name })}
				/>
				<now-tooltip target-ref={targetRef} content={text} />
			</Fragment>
		);
	},
	initialState: {
		targetRef: null
	},
	properties: {
		/**
		 * The text for the tooltip
		 * @type {string}
		 */
		text: {
			default: ''
		},
		/**
		 * A unique name for the tooltip. This is used only for a name when emitting
		 * onmouseenter and onmouseleave events.
		 * @type {string}
		 */
		name: {
			default: ''
		}
	},
	actionHandlers: {
		[SET_TARGET_REF]: ({ updateState, action }) => {
			const { payload } = action;

			updateState({
				targetRef: payload
			});
		}
	},
	styles
});
