import { createCustomElement } from '@servicenow/ui-core';

import styles from './_devx-checkbox.scss';

import '@servicenow/now-icon';
import '../devx-tooltip-icon';

export const CHECKED = 'DEVX_CHECKBOX#CHECKED';

/**
 * Checkbox
 *
 * ```jsx
 * <devx-checkbox />
 * ```
 *
 * @seismicElement devx-checkbox
 * @summary Checkbox
 */
createCustomElement('devx-checkbox', {
	view: (state, { dispatch }) => {
		const { checked, label, tooltip, disabled } = state.properties;

		return (
			<div className="now-align -block-center">
				<label
					class={{
						'devx-checkbox': true,
						'now-align': true,
						'-inline-start': true,
						'-is-disabled': disabled
					}}>
					<input
						className="now-a11y-label"
						type="checkbox"
						checked={checked}
						value={label}
						disabled={disabled}
						on-change={() => {
							dispatch(CHECKED, { value: !checked });
						}}
					/>

					<span className="devx-checkbox-icon">
						<now-icon icon="check-fill" size="md" />
					</span>

					{label ? <span className="devx-checkbox-label">{label}</span> : null}
				</label>

				{tooltip ? <devx-tooltip-icon text={tooltip} /> : null}
			</div>
		);
	},
	properties: {
		/**
		 * Determines if the checkbox is selected or not
		 *
		 * @type {boolean}
		 */
		checked: {
			reflect: true,
			default: false
		},
		/**
		 * Display label text for the checkbox
		 *
		 * @type {string}
		 */
		label: {
			default: ''
		},
		/**
		 * String text for a tooltip. If tooltip text isn't provided then it will not be rendered
		 *
		 * @type {string}
		 */

		tooltip: {
			default: ''
		},
		/**
		 * Controls whether the checkbox is in a disabled state that prevents the user from selecting
		 *
		 * @type {boolean}
		 */
		disabled: {
			reflect: true,
			default: false
		},
		/** @private */
		ariaChecked: {
			reflect: true,
			computed(state) {
				return state.properties.checked;
			}
		},
		/** @private */
		ariaDisabled: {
			reflect: true,
			computed(state) {
				return state.properties.disabled;
			}
		}
	},

	actionHandlers: {
		/**
		 * Dispatched when a user selects and unselects the checkbox. Payload contains
		 * a boolean of `true` when the user selects the checkbox and false when unselectd.
		 *
		 * @type {{value: boolean}}
		 */
		[CHECKED]: ({ action, dispatch }) => {
			dispatch('PROPERTIES_SET', { checked: action.payload.value });
		}
	},
	styles,

	// NOTE: This is used only for generating documentation
	dispatches: {
		/**
		 * Dispatched when the checkbox is checked or unchecked
		 * @type {{ value: boolean }}
		 */
		'DEVX_CHECKBOX#CHECKED': {}
	}
});
