import { createCustomElement } from '@servicenow/ui-core';
import '@servicenow/now-button';
import { filterAriaAttributes } from '@servicenow/library-enhanced-utils';
import isEmpty from 'lodash/isEmpty';
import styles from './_devx-input.scss';

export const INPUT_BLUR = 'DEVX_INPUT#INPUT_BLUR';
export const INPUT_CHANGED = 'DEVX_INPUT#INPUT_CHANGED';
export const INPUT_CLICKED = 'DEVX_INPUT#INPUT_CLICKED';
export const INPUT_FOCUS = 'DEVX_INPUT#INPUT_FOCUS';
export const INPUT_ICON_BUTTON_CLICKED = 'DEVX_INPUT#ICON_BUTTON_CLICKED';

import './../devx-tooltip-icon';

/**
 * Input
 *
 * ```jsx
 * <devx-input name="field-name" />
 * ```
 *
 * @seismicElement devx-input
 * @summary Input
 */
createCustomElement('devx-input', {
	// TODO SEISMIC BUG: Extra PROPERTIES_SET dispatched when value is controlled from form level, and overwrites the errorMsg prop on the input
	properties: {
		/**
		 * The type of the input. The supported types are the
		 * [native input's
		 * types](https://developer.mozilla.org/en-US/docs/Web/HTML/Element/input#Form_<input>_types).
		 * @type {string}
		 */
		type: { default: 'text' },
		/**
		 * Label to be displayed above the input field.
		 * @type {string}
		 */
		label: {},
		/**
		 * Aria properties to be added to the input.
		 * aria-invalid property is already being set on the input
		 * setting aria-invalid through configAria will override the current
		 * @type {{ 'aria-*': string }}
		 */
		configAria: { cast: 'object' },
		/**
		 * Secondary label to be displayed above the input field, but below the
		 * label.
		 * @type {string}
		 */
		secondaryLabel: {},
		/**
		 * Size of the input field.
		 * If none is provided, input is sized automatically.
		 * @type {( sm | md | lg )}
		 */
		size: {},
		/**
		 * A placeholder string to be displayed inside of the input field.
		 * @type {string}
		 */
		placeholder: {},
		/**
		 * Help or hint text to display below the input field.
		 * @type {string}
		 */
		helpText: {},
		/**
		 * Error message to display, sets error styles
		 * Also sets the aria-invalid property
		 * @type {string}
		 */
		errorMsg: {},
		/**
		 * The value for this input.
		 * @type {string}
		 */
		inputValue: { reflect: true },
		/**
		 * A name for the input control, passed along with the value when the form data is submitted.
		 * And used when passing in error state to the form
		 * @type {string}
		 */
		name: {},
		/**
		 * Set to true to disable this input.
		 * @type {boolean}
		 */
		disabled: { default: false },
		/**
		 * Set this flag to make the field read-only instead of interactive.
		 * @type {boolean}
		 */
		readonly: { default: false },
		/**
		 * Set to true to mark the input as required.
		 * @type {boolean}
		 */
		required: { default: false },
		/**
		 * The minimum length of the input value.
		 * @type {number}
		 */
		minlength: {},
		/**
		 * The maximum length of the input value.
		 * @type {number}
		 */
		maxlength: {},
		/**
		 * The minimum (numeric or date-time) input value.
		 * Can be a String (e.g. `"2000-01-01"`) or a Number (e.g. `2`).
		 * @type {(string|number)}
		 */
		min: {},
		/**
		 * The maximum (numeric or date-time) input value.
		 * Can be a String (e.g. `"2000-01-01"`) or a Number (e.g. `2`).
		 * @type {(string|number)}
		 */
		max: {},
		/**
		 * Limits the numeric or date-time increments.
		 * @type {(string|number)}
		 */
		step: {},
		/**
		 * Text to display when tooltip icon is hovered
		 * @type {string}
		 */
		tooltip: { default: '' },
		/**
		 * Flag for omitting space below input. WILL prevent any errorMsg or helpText from displaying
		 * @type {boolean}
		 */
		compact: { default: false, reflect: true },
		/**
		 * Flag for setting the background color of the input to transparent.
		 * @type {boolean}
		 */
		transparent: { default: false, reflect: true },
		/**
		 * A ServiceNow icon value to be used as a button in the input
		 * @type {string}
		 * @example 'magnifying-glass-outline'
		 */
		icon: { default: null },
		/**
		 * Any custom role to be used for accessibility purposes
		 * @type {string}
		 * @example 'combobox'
		 */
		overrideRole: { default: null }
	},
	initialState: {
		focused: false
	},
	view: (state, { dispatch }) => {
		const {
			type,
			label,
			size,
			placeholder,
			helpText,
			errorMsg,
			inputValue,
			name,
			disabled,
			readonly,
			pattern,
			required,
			maxlength,
			minlength,
			min,
			max,
			secondaryLabel,
			step,
			compact,
			tooltip,
			transparent,
			icon,
			overrideRole,
			configAria
		} = state.properties;
		const { componentId, focused } = state;

		return (
			<div
				class={{
					container: true,
					'-show-error': !!errorMsg,
					'-compact': compact,
					'-transparent': transparent
				}}>
				{label && (
					<div className="now-align">
						<label
							className="devx-input-label now-m-inline-end--xs"
							htmlFor={componentId}>
							{label}
						</label>
						{tooltip ? <devx-tooltip-icon text={tooltip} /> : null}
					</div>
				)}

				{secondaryLabel && (
					<div className="devx-input-label -secondary">{secondaryLabel}</div>
				)}

				<div className="devx-input-input-container">
					<input
						role={overrideRole || 'textbox'}
						class={{
							'devx-input': true,
							['-' + size]: size
						}}
						disabled={disabled}
						id={componentId}
						title={errorMsg ? errorMsg : ''}
						max={max}
						maxlength={maxlength}
						min={min}
						minlength={minlength}
						name={name}
						on-blur={(e) => {
							dispatch(INPUT_BLUR, { value: e.target.value, input: name });
						}}
						on-focus={(e) => {
							dispatch(INPUT_FOCUS, { value: e.target.value, input: name });
						}}
						on-input={(e) => {
							dispatch(INPUT_CHANGED, { value: e.target.value, input: name });
						}}
						on-click={() => {
							dispatch(INPUT_CLICKED);
						}}
						pattern={pattern}
						placeholder={placeholder}
						readonly={readonly}
						required={required}
						step={step}
						type={type}
						value={inputValue}
						{...filterAriaAttributes(configAria)}
						aria-invalid={errorMsg ? 'true' : 'false'}
					/>
					{icon ? (
						<span
							class={{
								'button-icon-container': true,
								error: !isEmpty(errorMsg),
								focus: focused,
								disabled: disabled
							}}>
							<now-button-iconic
								bare
								disabled={disabled}
								icon={icon}
								primary
								size="md"
								tabindex="-1"
							/>
						</span>
					) : null}
				</div>

				{compact ? null : (
					<span className="devx-input-help-text">{errorMsg || helpText}</span>
				)}
			</div>
		);
	},
	actionHandlers: {
		'NOW_BUTTON_ICONIC#CLICKED': ({ dispatch, action }) => {
			action.stopPropagation();
			/**
			 * An action that is dispatched from input when the icon
			 * button is present and is clicked
			 * @type {void}
			 */
			dispatch(INPUT_ICON_BUTTON_CLICKED);
		},
		[INPUT_FOCUS]: ({ updateState }) => {
			updateState({
				focused: true
			});
		},
		[INPUT_BLUR]: ({ updateState }) => {
			updateState({
				focused: false
			});
		}
	},
	styles
});
